import { BadRequestException } from '@nestjs/common';

import {
  File,
  IUpdateProjectCommonReport,
  ProjectCommonReport,
} from '../../../domain/entity';
import {
  CreateUserId,
  FileName,
  FilePath,
  ProjectId,
  ProjectReportId,
  ReportText,
  Title,
  UploadUserId,
  YearMonthDay,
} from '../../../domain/value';
import { CustomLogger } from '../../../utility';
import {
  ICreateProjectCommonReportRequest,
  IProjectCommonReportResponse,
  IUpdateProjectCommonReportRequest,
} from '../../interface';

export class ProjectCommonReportParser {
  constructor(readonly logger: CustomLogger) {}

  convertToProjectReport(
    input: ICreateProjectCommonReportRequest,
  ): ProjectCommonReport {
    try {
      const id = ProjectReportId.make();
      const createUserId = CreateUserId.make();
      const reportDate = YearMonthDay.from(input.report_date);
      const projectId = ProjectId.from(input.project_id);
      const uploadUserId = UploadUserId.from(createUserId.value);
      const files = input.attached_files.map(
        (file) =>
          new File({
            name: FileName.from(file.file_name),
            path: FilePath.from(file.file_path),
            uploadUserId,
          }),
      );
      const title = Title.from(input.title ?? null);
      const reportText = ReportText.from(input.report_text ?? null);

      return new ProjectCommonReport({
        id,
        createUserId,
        reportDate,
        projectId,
        files,
        title,
        reportText,
      });
    } catch (error) {
      this.logger.error(error);
      throw new BadRequestException(error.message);
    }
  }

  convertProjectReportToReference(
    entity: ProjectCommonReport,
  ): IProjectCommonReportResponse {
    const {
      id,
      files,
      projectId,
      reportDate,
      createUserId,
      title,
      reportText,
    } = entity;

    return {
      id: id.value,
      title: title.value ?? undefined,
      report_text: reportText.value ?? undefined,
      attached_files: files.map((file) => ({
        file_name: file.name.value,
        file_path: file.path.value,
      })),
      project_id: projectId.value,
      report_date: reportDate.toString(),
      create_user_id: createUserId.value,
    };
  }

  private returnValueOrUndefined<E, T>(
    value: E,
    operation: (value: E) => T,
  ): T | undefined {
    if (value === undefined) {
      return undefined;
    }
    return operation(value);
  }
  formatDataToUpdate(
    userId: string,
    data: IUpdateProjectCommonReportRequest,
  ): IUpdateProjectCommonReport {
    const uploadUserId = UploadUserId.from(userId);
    return {
      files: data.attached_files?.map(
        (file) =>
          new File({
            name: FileName.from(file.file_name),
            path: FilePath.from(file.file_path),
            uploadUserId,
          }),
      ),

      title: this.returnValueOrUndefined(data.title, Title.from),
      reportText: this.returnValueOrUndefined(
        data.report_text,
        ReportText.from,
      ),

      reportDate: this.returnValueOrUndefined(
        data.report_date,
        YearMonthDay.from,
      ),
    };
  }
}
